@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;600;700;900&display=swap");
.section_Dimentions {
  padding: 70px 0 40px 0;
}

.inner_container {
  padding: 0 20px;
}

.section_Title {
  font-size: 2em;
  display: inline-block;
  padding-bottom: 10px;
  margin-bottom: 30px;
  position: relative;
  color: #002651;
}

.section_Title::before {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 2px;
  background: #28c7fa;
  transform: translateY(-50%);
}

.dropdown-item {
  transition: .2s;
}

.dropdown-item:hover {
  background-color: #dfdfdf;
  color: #1a1a1a;
}

.navbar-brand img {
  width: 80px;
}

.banar img {
  width: 100%;
}

.banar h2 {
  font-size: 48px;
}

.service-block {
  margin: 40px 0;
}

.service-block h4 {
  color: #0078d4;
}

.service-block img {
  width: 100%;
}

.carousel {
  height: calc(100vh - 50px);
}

.carousel ol.carousel-indicators li {
  margin: 0 5px;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  transition: all .2s ease-in-out;
}

.carousel .carousel-inner {
  height: 100%;
}

.carousel .carousel-inner .carousel-item {
  height: 100%;
  position: relative;
}

.carousel .carousel-inner .carousel-item img {
  height: 100%;
}

.carousel .carousel-inner .carousel-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.carousel .carousel-inner .carousel-item .carousel-caption {
  background-color: rgba(37, 47, 49, 0.8);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  height: min-content;
}

.carousel .carousel-inner .carousel-item .carousel-caption .title {
  color: #28c7fa;
}

.carousel .carousel-inner .carousel-item .carousel-caption p {
  width: 90%;
  margin: auto;
}

.carousel a.carousel-control-prev,
.carousel a.carousel-control-next {
  background-color: rgba(0, 0, 0, 0.5);
  width: 5%;
}

@media (max-width: 575px) {
  .carousel .carousel-inner .carousel-item .carousel-caption {
    top: 20px;
    width: 70%;
    height: min-content;
    transform: translateX(-50%);
  }
  .carousel .carousel-inner .carousel-item .carousel-caption .title {
    font-size: 28px;
  }
  .carousel .carousel-inner .carousel-item .carousel-caption p {
    font-size: 14px;
  }
  .carousel a.carousel-control-prev,
  .carousel a.carousel-control-next {
    background-color: rgba(0, 0, 0, 0.5);
    width: 10%;
  }
}

.boxes {
  box-sizing: border-box;
  text-align: center;
}

.boxes .box {
  text-align: left;
  position: relative;
  background: #fff;
  padding: 100px 20px 20px;
  border-radius: 10px;
  box-shadow: 0 15px 22px rgba(0, 0, 0, 0.2), 0 -15px 22px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  transition: .5s;
}

.boxes .box h2 {
  position: absolute;
  left: 20px;
  top: 30px;
  font-size: 4em;
  font-weight: 800;
  z-index: 1;
  opacity: .2;
  transition: .5s;
  text-align: center;
}

.boxes .box h3 {
  position: relative;
  font-size: 1.5em;
  z-index: 2;
  color: #333;
  transition: .5s;
}

.boxes .box p {
  position: relative;
  z-index: 2;
  color: #555;
  transition: .5s;
}

.boxes .box.one::before {
  background: #28c7fa;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0.03);
  transform-origin: bottom;
  transition: transform 0.5s;
  z-index: 0;
}

.boxes .box.two::before {
  background: #775ada;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0.03);
  transform-origin: bottom;
  transition: transform 0.5s;
  z-index: 0;
}

.boxes .box.three::before {
  background: #002651;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0.03);
  transform-origin: bottom;
  transition: transform 0.5s;
  z-index: 0;
}

.boxes .box.four::before {
  background: #ff304f;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0.03);
  transform-origin: bottom;
  transition: transform 0.5s;
  z-index: 0;
}

.boxes .box:hover {
  transform: scale(1.1, 1.1);
}

.boxes .box:hover h2 {
  opacity: 1;
  color: #fff;
  left: 50%;
  transform: translateY(-15px) translateX(-50%) scale(1.1);
}

.boxes .box:hover h3,
.boxes .box:hover p {
  color: #fff;
}

.boxes .box:hover::before {
  transform: scaleY(1);
  transform-origin: bottom;
}

body {
  text-transform: capitalize;
  font-family: 'Cairo', sans-serif;
  overflow-x: hidden;
  padding-top: 50px;
}

.about .about-text {
  display: flex;
  align-items: center;
  height: 100%;
}

.about .about-text p {
  color: #555;
  line-height: 1.8;
}

.about .about-image {
  position: relative;
}

.about .about-image img {
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.2);
  width: 80%;
  position: relative;
  z-index: 1;
  display: block;
  margin: auto;
  border-radius: 10px;
}

.about .about-image::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  height: 100%;
  background: #28c7fa;
  border-radius: 10px;
}

.statistics .statis:hover i {
  border-radius: 10px;
}

.statistics .statis h4 {
  margin: 0;
  font-size: 26px;
  color: #666;
}

.statistics .statis span {
  display: block;
  font-size: 22px;
  color: #888;
}

.statistics .statis i {
  font-size: 60px;
  color: #fff;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  display: block;
  margin: auto;
  transition: .2s;
}

.statistics .statis.one i {
  background-color: #28c7fa;
}

.statistics .statis.two i {
  background-color: #775ada;
}

.statistics .statis.three i {
  background-color: #002651;
}

.statistics .statis.four i {
  background-color: #ff304f;
}

.portfolio .portfolio-item {
  position: relative;
  overflow: hidden;
  text-align: center;
}

.portfolio .portfolio-item:hover img {
  transform: scale(1.1);
}

.portfolio .portfolio-item img {
  width: 100%;
  border: 1px solid #ddd;
  transition: all .3s;
}

.portfolio .portfolio-item:first-of-type {
  float: left;
  width: 50%;
}

.portfolio .portfolio-item:first-of-type img {
  height: 300px;
}

.portfolio .portfolio-item:first-of-type > i {
  font-size: 24px;
}

.portfolio .portfolio-item:nth-of-type(2), .portfolio .portfolio-item:nth-of-type(3), .portfolio .portfolio-item:nth-of-type(5), .portfolio .portfolio-item:nth-of-type(6) {
  width: 25%;
  float: left;
}

.portfolio .portfolio-item:nth-of-type(2) img, .portfolio .portfolio-item:nth-of-type(3) img, .portfolio .portfolio-item:nth-of-type(5) img, .portfolio .portfolio-item:nth-of-type(6) img {
  height: 150px;
}

.portfolio .portfolio-item:nth-of-type(2) > i, .portfolio .portfolio-item:nth-of-type(3) > i, .portfolio .portfolio-item:nth-of-type(5) > i, .portfolio .portfolio-item:nth-of-type(6) > i {
  font-size: 16px;
}

.portfolio .portfolio-item:nth-of-type(4) {
  width: 50%;
  float: right;
}

.portfolio .portfolio-item:nth-of-type(4) img {
  height: 300px;
}

.portfolio .portfolio-item:nth-of-type(4) > i {
  font-size: 24px;
}
